

/* eslint-disable no-unused-vars */


import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
} from "reactstrap"

import {
  CButton,
  CCard,
  CCardHeader,
  CCardBody,
  CCardFooter,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import Select from "react-select"
import axios from 'axios'
import  { Redirect } from 'react-router-dom'

import { French } from "flatpickr/dist/l10n/fr.js";

// import Flatpickr from "react-flatpickr"
import Flatpickr from "flatpickr";

import DatePicker from "react-datepicker";
import { registerLocale, setDefaultLocale } from  "react-datepicker";
import es from 'date-fns/locale/es';
import $ from 'jquery';
import conf from '../../../conf.js';
import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';
import ville from "../../dict/ville.js";
import { storage } from "../../../firebase/firebase";
import ModalMessage from "../../pages/messages/ModalMessage"
const animatedComponents = makeAnimated();


// const cities = require('all-the-cities');

registerLocale('es', es)


  const optionGroup = [
    {
      
      options: [
        { label: "PDF", value: "PDF" },
        { label: "MP4", value: "MP4" },
        { label: "JPEG", value: "JPEG" },
        { label: "MP3", value: "MP3" },
        { label: "EXCEL", value: "EXCEL" },
        
      ]
    },
    
  ]
  const optionLangue = [
    {
      options: [
        { label: "Francais", value: "Francais" },
        { label: "Anglais", value: "Anglais" },
        { label: "Wolof", value: "Wolof" }
      ]
    },
  ]
  
class AjoutTerrain extends Component {
    constructor(props) {
        super(props)
        this.state = {
          user:{},
          users:[],
          ville:"",
          ok:"",
          role:"",
          files_preview:[],
          files_name:[],
          multimedia:[],
          show:false,
          success:true,
          boutton:"OK",
          message:"Terrain ajout avec succs !!",
          titre:"Message ajout de terrain",
          type:"PRIVE"
        }
        this.handleChangeVille.bind(this)
        this.handleChangeType.bind(this)
        this.handleSavePrice.bind(this)
    }

    componentDidMount() {
      const myHeaders = new Headers();
      
       let  user=  JSON.parse(localStorage.getItem('token_auth'))

       this.setState({role:user.role})
     $("#chargement").css("display","none")
     
     const requestOptions = {
      headers: { 
        'Authorization':'Bearer '+user.token,
  
      },
  
      'method':'GET'
     };
     
     fetch(conf.url+"user/users/"+user.id,requestOptions)
     .then((response) => response.json())
     .then((data) => {
        console.log("okkk====>",data)
        try{
         if(data.id){
         
            this.setState({user:data})
          
         }else{
           this.setState({user:{}})
         }
        }catch(error){
         this.setState({user:{}})
        }
         
     
 
      
      }
      
     );
  
      fetch(conf.url+"user/users",requestOptions)
      .then((response) => response.json())
      .then((data) => {
         console.log("okkk====>",data)
         try{
          if(data.length!=0){
            data.map((element,idx)=>{
             this.setState({users:[...this.state.users,{label:element.username,value:element}]})
            })
          }else{
            this.setState({users:[]})
          }
         }catch(error){
          this.setState({users:[]})
         }
          
      
  
       
       }
       
      );


      $("input[data-preview]").change(() =>{
        var data=[]
        console.log("fiel =====>",Array.from(document.getElementById("autreFile").files))
        var date=new Date()
        this.setState({files_preview:data,files_name:data,multimedia:data})
        var my_files = Array.from(document.getElementById("autreFile").files)
        my_files.map((element,idx)=>{
          var fileNom=date.getFullYear()+""+date.getMonth()+""+date.getDay()+""+date.getSeconds()+""+date.getMilliseconds()+""+element.name
          this.setState({files_name:[...this.state.files_name,{file:element,fileName:fileNom}],multimedia:[...this.state.multimedia,{file:fileNom}]})
          
          var fileReader = new FileReader();
          fileReader.readAsDataURL(element);
          fileReader.onload = (fileEvent)=> {
            this.setState({files_preview:[...this.state.files_preview,fileEvent.target.result]})
      
          };

        })
    
      });
    }

    handleChangeVille = (e) => {
     
      try{
        this.setState({ville:e.label})
       
      }catch(ee){
  
      }
    
    }

    handleChangeType = (e) => {
     
      try{
        this.setState({type:e.label})
       
      }catch(ee){
  
      }
    
    }

    handleChangeUser = (e) => {
     
      try{
        this.setState({user:e.value})
       
      }catch(ee){
  
      }
    
    }


    handleAddTerrain=()=>{

      const jours=["LUNDI","MARDI","MERCREDI","JEUDI","VENDREDI","SAMEDI","DIMANCHE"]

       
      this.state.files_name.map((el,idx)=>{
        storage.ref("images/"+el.fileName).put(el.file).then(()=>{})
      })
      
      let  token=  JSON.parse(localStorage.getItem('token_auth'))
      var data= {
        "nom": $("#nom").val(),
        "adresse": $("#adresse").val(),
        "ville":this.state.ville,
        "latitude":$("#latitude").val(),
        "longitude": $("#longitude").val(),
        "disponibilite": $("#disponibilite").val(),
        "description": $("#description").val(),
        "prixHeure":$("#prixheure").val(),
        "prixDemiHeure":$("#prixdemiheure").val(),
        "disponibilite":"7h-18h",
        "multimedias":this.state.multimedia,
        "user":null,//this.state.user,
        "reservations":null,
         "statusTerrain":this.state.type,
         "idUser":this.state.user.id,
         "dateInscription":new Date()
      
       
    
      }
  
      console.log("data a transmettre",data)
  
      const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json','Accept': 'application/json',
        'Authorization':'Bearer '+token.token,
      },
        body: JSON.stringify(data)
    };
  
    fetch(conf.url+"terrain/add/"+this.state.user.id,requestOptions)
    .then((response) => response.json())
    .then((data) => {
       console.log("okkk====>",data)

       try{
        if(data.id){
         
         for(var i=1;i<=7;i++){
            this.handleSavePrice({typeDePrix:"heure",valeur:$("#"+i).val(),jour:jours[i-1]},data.id)
            if(i==7){
              this.setState({show:true,success:true});
            }
         }
        }else{
         this.setState({show:true,success:false});
        }
     }catch(err){
       this.setState({show:true,success:false});
     }
       
    
     }
     
    );
      
    } 
    
    
    handleSavePrice=(data,id)=>{
      let  token=  JSON.parse(localStorage.getItem('token_auth'))
      const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json','Accept': 'application/json',
        'Authorization':'Bearer '+token.token,
      },
        body: JSON.stringify(data)
    };


    fetch(conf.url+"user/add/prix/terrain/"+id,requestOptions)
    .then((response) => response.json())
    .then((res) => {
    })
      
    }

    handleValidate=()=>{
      if(this.state.success){
        this.setState({ok: <Redirect to='/liste-terrain'/>});
      }else{
        this.setState({show:false})
      }
      
    }
 render() {

  const { pick } = this.state
  const { selectedGroup } = this.state
  const { selectedLangue } = this.state
  const animatedComponents = makeAnimated();

  const typeDeTerrain = [
    {
      value: "PRIVE", 
      label:"PRIVE"
    },
    {
      value:"PUBLIC", 
      label:"PUBLIC"
    }
  ];

  return (
        <>
          <React.Fragment>
            <div className="page-content">
             {this.state.ok}
             <ModalMessage
              show={this.state.show}
              success={this.state.success}
              boutton={this.state.boutton}
              handleValidate={this.handleValidate}
              message={this.state.message}
              titre={this.state.titre}
              onCloseClick={() => this.setState({ show: false })}
              />
            <Container fluid={false} className="justify-content-center">

              <Row className="justify-content-center">
              <Col md="12" lg="12" xl="12">

                {/* <Row ClassName="mb-3">
                  <Col md="8">
                      <div className="page-title-box d-flex align-items-center justify-content-between">
                        <h4 className="mb-0 font-size-18">Nouveau compte</h4>
                      </div>
                  </Col>
                </Row> */}
     
                

                <Card ClassName="mt-5">
                  {/* <CCardHeader className="p-4"> */}
                    <h1 className="p-4" >Nouveau terrain</h1>
                  {/* </CCardHeader> */}

                  <CardBody>
                    <Row> 
                      <Col md="4">
                        <label>Nom</label>
                        <Input
                              className="form-control"
                              type="text"
                              id="nom"
                              placeholder="Nom"
                          />
                      </Col>
                      <Col md="4">
                        <label>Adresse</label>
                        <Input
                              className="form-control"
                              type="text"
                              id="adresse"
                              placeholder="Adresse"
                          />
                      </Col>
                      <Col md="2">
                        <label>Ville</label>
                        <ReactSelect
                          closeMenuOnSelect={true}
                          components={animatedComponents}
                          onChange={this.handleChangeVille}
                          options={ville}
                          placeholder="Ville"
                        />
                      </Col>
                      <Col md="2">
                        <label>Type de terrain</label>
                        {
                          (this.state.role!="PROPRIETAIRE_TERRAIN")?
                          <ReactSelect
                          closeMenuOnSelect={true}
                          components={animatedComponents}
                          onChange={this.handleChangeType}
                          options={typeDeTerrain}
                          placeholder="Type de terrain"
                        />:
                         <Input
                            className="form-control"
                            type="text"
                            value="PRIVE"
                            readOnly
                        
                         />
                         }
                      </Col>
                    </Row>
                  
                    <Row className="mt-4">
                      <Col md="12">
                        <label>Description</label>
                        <textarea  className="form-control" id="description" placeholder="Description" />
                        
                      </Col>
                    </Row>

                    <Row className="mt-4">
                      <Col md="6">
                        <label>Latitude</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="latitude"
                          placeholder="Latitude"
                        />
                      </Col>
                      <Col md="6">
                      <label>Longitude</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="longitude"
                          placeholder="Longitude"
                        />
                      </Col>
                    </Row>

                    {/* <Row className="mt-4">
                      <Col md="6">
                        <label>Prix par heure</label>
                        <Input
                          className="form-control"
                          type="number"
                          min="0"
                          id="prixheure"
                          placeholder="Prix par heure"
                        />
                      </Col>
                      <Col md="6">
                      <label>Prix par demi-heure</label>
                        <Input
                          className="form-control"
                          type="number"
                          min="0"
                          id="prixdemiheure"
                          placeholder="Prix par demi-heure"
                        />
                      </Col>
                    </Row> */}
                    
                    
                    <Row className="mt-4">
                    <Col md="6">
                      <label>Image(s)</label>
                      <Input
                          className="form-control"
                          type="file"
                          multiple 
                          data-preview=".preview"
                          placeholder="Photo"
                          id="autreFile"
                      />
                      </Col>
                      <Col md="6">
                      <label>Propritaire</label>
                        
                        {
                          (this.state.role!="PROPRIETAIRE_TERRAIN")?
                          <ReactSelect
                          closeMenuOnSelect={true}
                          components={animatedComponents}
                          onChange={this.handleChangeUser}
                          options={this.state.users}
                          placeholder="Propritaire"
                        />:
                        <Input
                        className="form-control"
                        type="text"
                        value={this.state.user.prenom}
                        readOnly
                        
                        />
                      }
                      </Col>
                    </Row>
                    <hr/>
                    <Row className="mt-4">
                       
                        <Col md="2">  
                          <label>Prix Lundi</label>
                          <Input
                              className="form-control"
                              type="number"
                              min="0"
                              placeholder="Prix de lundi"
                              id="1"
                          />
                         </Col>
                       
                        <Col md="2">  
                        <label>Prix Mardi</label>
                          <Input
                              className="form-control"
                              type="number"
                              min="0"
                              placeholder="Prix de mardi"
                              id="2"
                          />
                         </Col>

                         <Col md="2">
                          <label>Prix Mercredi</label>
                          <Input
                              className="form-control"
                              type="number"
                              min="0"
                              placeholder="Prix de mercredi"
                              id="3"
                          />
                        </Col>
                        <Col md="2">  
                        <label>Prix Jeudi</label>
                          <Input
                              className="form-control"
                              type="number"
                              min="0"
                              placeholder="Prix de jeudi"
                              id="4"
                          />
                        </Col>
                        <Col md="2">
                          <label>Prix Vendredi</label>
                          <Input
                              className="form-control"
                              type="number"
                              min="0"
                              placeholder="Prix de vendredi"
                              id="5"
                          />
                        </Col>
                        <Col md="2">  
                        <label>Prix Samedi</label>
                          <Input
                              className="form-control"
                              type="number"
                              min="0"
                              placeholder="Prix de samedi"
                              id="6"
                          />
                        </Col>
                    </Row>
                    <Row className="mt-4">
                       
                       <Col md="2">  
                         <label>Prix dimanche</label>
                         <Input
                             className="form-control"
                             type="number"
                             min="0"
                             placeholder="Prix de dimanche"
                             id="7"
                         />
                        </Col>
                      
                      </Row>
                    
                      {
                        this.state.files_preview.map((element,idx)=>(
                          <Row className="mt-2">
                             <Col md="4">
                                    <Card>
                                       
                                        <CardBody>
                                            <img style={{width:"900px",heigth:"300px"}} className="card-img-top" src={element} alt="Image"/>
                                        </CardBody>
                                        
                                       
                                    </Card>
                              </Col>
                          </Row>
                        )
                        )

                      }
                     
                    
                    


                    <Row className="justify-content-start mt-5">
                      <Col md="6">
                        <Button id="initial" onClick={this.handleAddTerrain} color="success" block className="b-lg" >
                          Ajouter le terrain
                        </Button>
                        {/* <Button id="chargement" color="info" className="b-lg" type="button" disabled>
                        <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"> </span>
                          Envoi...
                        </Button> */}
                      </Col>
                      <Col md="6">
                        <Button  color="secondary" block className="b-lg">
                          Annuler
                        </Button>
                      </Col>
                      
                    </Row>
                  </CardBody>
                </Card>
              </Col>
            </Row>
          </Container>
        </div>
             
      </React.Fragment>
    </>
        )}

  }
  export default AjoutTerrain

